import React, { useState, useEffect } from 'react';
import axios from 'axios';
import * as XLSX from 'xlsx';
import { FaFileExcel } from 'react-icons/fa';
import io from 'socket.io-client';
import './ExcelEditor.css';

const socket = io('http://localhost:5000');

const ExcelEditor = () => {
  const [selectedFile, setSelectedFile] = useState(null);
  const [table, setTable] = useState('');
  const [schema, setSchema] = useState([]);
  const [message, setMessage] = useState('');
  const [matchingColumns, setMatchingColumns] = useState(0);
  const [uploadProgress, setUploadProgress] = useState(0);

  useEffect(() => {
    if (table) {
      const fetchSchema = async () => {
        try {
          const response = await axios.get(`http://localhost:5000/api/schema/${table}`, {
            headers: {
              Authorization: `Bearer ${localStorage.getItem('token')}`,
            },
          });
          setSchema(response.data);
        } catch (error) {
          setMessage('Erreur lors de la rcupration du schma');
          console.error('Error fetching schema:', error);
        }
      };
      fetchSchema();
    }
  }, [table]);

  useEffect(() => {
    socket.on('uploadProgress', ({ uploadId, progress }) => {
      setUploadProgress(progress);
    });

    return () => {
      socket.off('uploadProgress');
    };
  }, []);

  const handleFileChange = (event) => {
    setSelectedFile(event.target.files[0]);
  };

  const handleUpload = async () => {
    try {
      if (!selectedFile || !table) {
        alert('Veuillez slectionner un tableau et un fichier.');
        return;
      }

      const formData = new FormData();
      formData.append('file', selectedFile);

      const response = await axios.post(`http://localhost:5000/api/upload/${table}`, formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
          Authorization: `Bearer ${localStorage.getItem('token')}`,
        },
      });

      console.log('File uploaded successfully:', response.data);
      setMessage('Fichier tlcharg avec succs');
    } catch (error) {
      console.error('Error uploading file:', error);
      alert('Erreur lors du tlchargement du fichier: ' + error.message);
      setMessage('Erreur lors du tlchargement du fichier: ' + error.message);
    }
  };

  const filterColumns = (data) => {
    if (schema.length === 0) return data;
    return data.map(record => {
      return Object.fromEntries(
        Object.entries(record).filter(([key]) => schema.includes(key))
      );
    });
  };

  const countMatchingColumns = (data) => {
    if (schema.length === 0 || data.length === 0) return 0;
    return schema.filter(column => Object.keys(data[0]).includes(column)).length;
  };

  useEffect(() => {
    if (selectedFile) {
      const reader = new FileReader();
      reader.onload = (e) => {
        const workbook = XLSX.read(e.target.result, { type: 'binary' });
        const sheetName = workbook.SheetNames[0];
        const data = XLSX.utils.sheet_to_json(workbook.Sheets[sheetName]);
        const filteredData = filterColumns(data);
        setMatchingColumns(countMatchingColumns(filteredData));
      };
      reader.readAsBinaryString(selectedFile);
    }
  }, [selectedFile, schema]);

  return (
    <div className="excel-editor">
      <h2>Tlchargement de fichier Excel</h2>
      <div className="form-group">
        <label>Slectionnez le fichier Excel</label>
        <div className="file-upload">
          <input type="file" onChange={handleFileChange} />
          <div className="file-upload-text">
            {selectedFile ? (
              <>
                <FaFileExcel className="file-icon" />
                {selectedFile.name}
              </>
            ) : (
              <>Dposez vos fichiers ici ou <span className="browse-files">Parcourir les fichiers</span></>
            )}
          </div>
          <div className="file-type-text">Types de fichiers accepts : .xlsx</div>
        </div>
      </div>
      <div className="form-group">
        <label>Slectionnez un tableau</label>
        <select value={table} onChange={(e) => setTable(e.target.value)}>
          <option value="">Slectionnez un tableau</option>
          <option value="it_equipments">quipements informatiques</option>
          <option value="telecom_pack">Parc Tlcom</option>
          <option value="telephone_lines">Lignes tlphoniques</option>
        </select>
      </div>
      <button onClick={handleUpload}>Tlcharger</button>
      <div className="progress-container">
        <div className="progress">
          <div className="bar" style={{ width: `${uploadProgress}%` }}>
            <p className="counter">{uploadProgress}%</p>
          </div>
        </div>
        {uploadProgress === 100 && (
          <div className="success-container">
            <p className="success">Tlchargement termin!</p>
            <p className="success-message">Fichier tlcharg avec succs</p>
          </div>
        )}
      </div>
      {message && <p>{message}</p>}
      {schema.length > 0 && (
        <div className="schema-container">
          <h3>Schma du tableau</h3>
          <p>Nombre de colonnes correspondantes : {matchingColumns}</p>
          <div className="schema-table">
            {schema.map((column) => (
              <div key={column} className="schema-column">
                {column}
                <div className="placeholder">
                  {column === 'numero_de_gsm' && 'Ex: 212XXXXXXXXX'}
                  {column.includes('date') && 'Format: yyyy-mm-dd ou mm/dd/yyyy'}
                </div>
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  );
};

export default ExcelEditor;
